import { Component, h, Host, Prop } from '@stencil/core';
import { createColorClasses } from '../../../utils/functions/color.function';
import { TpColor } from '../../../utils/types/color.type';

@Component({
  tag: 'tp-base',
  styleUrl: 'base.scss',
  scoped: true,
})
export class TpBase {
  /**
   * todo
   */
  @Prop({ reflect: true }) color?: TpColor;

  /**
   * todo
   */
  @Prop({ reflect: true }) radius?: 's00' | 's02' | 's04' | 's08' | string;

  /**
   * todo
   */
  @Prop({ reflect: true }) gap?: 's00' | 's02' | 's04' | 's08' | string;

  /**
   * todo
   */
  @Prop({ reflect: true }) spacingV?:
    | 's00'
    | 's02'
    | 's04'
    | 's08'
    | 's12'
    | 's16'
    | 's24'
    | string;

  /**
   * todo
   */
  @Prop({ reflect: true }) spacingH?:
    | 's00'
    | 's02'
    | 's04'
    | 's08'
    | 's12'
    | 's16'
    | 's24'
    | string;

  render() {
    const { color, radius, gap, spacingV, spacingH } = this;

    return (
      <Host
        class={createColorClasses(color, {
          'tp-base': true,
          [`tp-base--radius-${radius}`]: radius !== undefined,
          [`tp-base--gap-${gap}`]: gap !== undefined,
          [`tp-base--spacing-v-${spacingV}`]: spacingV !== undefined,
          [`tp-base--spacing-h-${spacingH}`]: spacingH !== undefined,
        })}
      >
        <slot name="start"></slot>
        <slot name="middle"></slot>
        <slot name="end"></slot>
        <slot></slot>
      </Host>
    );
  }
}
